<style lang="less" scoped>

    .filter{
        height: 100px;
        padding-top: 20px;
        .casContainer {
            width: auto;
            width: fit-content;
            
            .casInput {
                width: 250px;
                .ant-input {
                min-height: 32px;
                padding: 0 4px;
                display: flex;
                align-items: center;
                position: relative;

                .right-icon {
                    position: absolute;
                    right: 12px;
                }

                .tags {
                    max-width: 330px;
                    height: auto;
                    word-wrap: break-word;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;

                    .result {
                    width: 24px;
                    height: 24px;
                    margin-right: 20px;
                    background: #f5f5f5;
                    border-radius: 12px;
                    color: #333;
                    text-align: center;
                    position: absolute;
                    right: 22px;
                    }

                    .casNode {
                    height: auto;
                    background: #f5f5f5;
                    border-radius: 4px;
                    padding: 2px 8px;
                    margin-right: 4px;
                    margin: 2px 4px 2px 0px;
                    color: #333;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .tag {
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        width: auto;
                        margin-right: 4px;
                        display: inline-block;
                        text-align: center;
                    }
                    }
                }
                }
            }
            .casbox {
                position: relative;
                width: fit-content;
                margin-top: 4px;
                display: flex;
                box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.12);
                border-radius: 4px;
                z-index: 100;
                background: #fff;
                .boxItem {
                min-width: 250px;
                padding: 1px 0;
                height: 180px;
                border-right: 1px solid #eee;
                color: #333;

                .Item {
                    height: 34px;
                }

                .checked {
                    .ant-checkbox-wrapper {
                    color: #1bbc9b;
                    }
                }

                &:nth-child(1) {
                    border-radius: 4px 0px 0px 4px;
                }
                &:last-child {
                    border-radius: 0px 4px 4px 0px;
                    border: 0px;
                }
                }
            }

            .icon-arrow{
                transform: rotate(90deg);
                float: right;
            }


            .fadeOut {
                display: none;
            }
            .fadeIn {
                animation: fadeIn 0.25s;
            }

            @keyframes fadeIn {
                from {
                opacity: 0;
                height: 0;
                }
                to {
                opacity: 1;
                height: 180px;
                display: "";
                }
            }
            }
    }
    .ant-row{
        margin-bottom: 10px;
        .ant-col{
            .content{
                background: #FFFFFF;
                box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.12);
                border-radius: 4px;
                height: 120px;
                line-height: 20px;
                position: relative;
                background-size: cover;
                .mask{
                    color: #fff;
                    position: absolute;
                    top: 0;
                    left:0;
                    width: 100%;
                    height:100%;
                    background: rgba(0,0,0,0.5);
                    .name{
                        padding-top: 30px;
                    }
                    .author{
                        margin-top: 10px;
                    }
                    .del{
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        text-align: center;
                        color: red;
                        background: rgba(255,255,255,0.5);
                        height: 20px;
                        line-height: 20px;
                    }
                }
               
            }
            font-size: 13px;
            text-align: center;
        }

    }
     /* box 表单 */
    .flexForm{
        width: auto;
        text-align: center;
        margin-top: 20px;
        .formContainer{
            width: 100%;
            margin: 0 auto;
           
            .ant-form{
                .ant-form-item{
                   div.ant-form-item-label{
                       margin-right: 8px;
                       min-width: 100px;
                       text-align: right;
                   }
                }
            }

            .ant-form.left{
                .ant-form-item{
                   div.ant-form-item-label{
                       margin-right: 8px;
                       min-width: 100px;
                       text-align: left;
                   }
                }
            }

            .ant-checkbox-wrapper + .ant-checkbox-wrapper{
                margin-left: 0px;
                margin-right: 8px;
                height: 30px;
                line-height: 30px;
            }
        }
    }


</style>

<template>
    <div class="projects">

        <!-- 筛选区域 -->
        <div class="filter">
            <a-row>
                <a-col :span="7">
                    <div class="casContainer" ref="casContainer">
                        <div class="casInput">
                            <a-input v-model:value="value" placeholder="请选择项目" @focus="did" @change="search">
                                <template #suffix>
                                    <my-icon  type="icon-search-normal"></my-icon>
                                </template>
                            </a-input>
                        </div>

                        <div class="casbox" :class="[focus? ' fadeIn' : ' fadeOut']" v-if="list.length > 0">
                            <ul class="boxItem ant-cascader-menu">
                                <li @click="select(item)" class="Item ant-cascader-menu-item" v-for="(item, index) in list" :key="index">
                                    {{item.projectName}}
                                </li>
                            </ul>
                        </div>
                    </div>
                </a-col>
                <a-col :span="4">
                    <a-button @click="showBox= true" type="primary">添加项目</a-button>
                </a-col>
            </a-row>
        </div>

          
          <!--结果区域 -->
         <div class="result">
           
            <a-row :gutter="[16,16]" :justify="center">
                <a-col :span="spanValue" v-for="(item,index) in  page.projects" :key="index" >
                    <div class="content" :style="{backgroundImage: `url(${item.pics.length && item.pics[0].url || 'https://doc.pharmcube.com/image95aa7804060a4ccb9b5e26635c862d65'} )`}" @click="toPics(item)">
                        <div class="mask" @mouseenter="item.showDel=true" @mouseleave="item.showDel=false">
                            <div class="name">{{item.projectName}}</div>
                            <div class="author">
                                创建者 ： {{item.createrName}}
                            </div>
                            <div class="del" @click.stop="showDeleteConfirm(item)" v-if="item.showDel">删除</div>
                        </div>
                    </div>
                </a-col>
            </a-row>

        </div>

        <!-- 添加box -->
        <a-modal v-model:visible="showBox" title="添加项目">
            <template #footer>
                <a-button key="back" @click="showBox= false">取消</a-button>
                <a-button key="submit" type="primary" @click="onSubmit" >提交</a-button>
            </template>

            <div class="flexForm">
                <div class="formContainer">
                    <a-form ref="formRef" :model="formState" :rules="rules" :label-col="labelCol" :wrapper-col="wrapperCol" style="width: 100%;">
                        <a-row>
                            <a-col :span="24">
                                <a-form-item ref="proName" label="项目名称" name="proName">
                                    <a-input v-model:value="formState.proName" placeholder="请输入">
                                </a-input></a-form-item>
                            </a-col>
                        </a-row>
                    </a-form>

                </div>
            </div>
        </a-modal>
    </div>
</template>

<script>
    import {ref,reactive, onMounted, toRaw, createVNode} from 'vue'
    import { useRouter } from "vue-router";
    import {postRequest} from '@/utils/request';
    import { message, Modal } from 'ant-design-vue';
    import { ExclamationCircleFilled  } from "@ant-design/icons-vue";


    export default {
        name: 'img-projects',
        setup(props) {
            const page = reactive({
                 projects: [], // 项目集合
            }) 
            
             //初始化路由
            const router = useRouter();
            const showBox = ref(false)
            const list= ref([])
            const curIndex = ref(0)
            const focus = ref(false)
            const curValue = ref('')
            const value= ref('')
            let list_copy = []
            const formRef = ref();
           
            const formState = reactive({
                proName: '',
            });
            const rules = {
            proName: [
                {
                    required: true,
                    message: '请输入项目名称',
                    trigger: 'blur',
                },
            ]
            };
            //添加项目
            const onSubmit = () => {
                const userId = sessionStorage.getItem('userId')
                formRef.value
                    .validate()
                    .then(  async() => {
                        const {proName} = toRaw(formState)
                        console.log(userId,proName, '22' )
                        await postRequest(`/project/addProject`,{userId,name:proName})
                        showBox.value= false
                        formState.proName = ''
                        await getPros()
                    })
                    .catch(error => {
                        console.log('error', error);
                    });
            };


        

            /* 获取项目组 */
            const getPros = async ()=> {
                const userId =  sessionStorage.getItem('userId')
                const {list} = await postRequest( `/project/getPicProjects`,{userId})
                page.projects = list
                list_copy = JSON.parse(JSON.stringify(page.projects))
            }

            const delPro = async (item)=> {
                const {id} = item
                try{
                    await postRequest(`/project/setNoEffctPro`, {projectId : id })
                    await getPros()
                    message.success('删除成功！')
                }catch(err){
                    console.log(err, 'err')
                }
                
            }

            
            onMounted( async (options) =>{
                await getPros()
            })

            const did = () =>  {
                focus.value = !focus.value
                if (focus.value) {
                    curIndex.value = 0
                }
            }

            /* 选中 */
            const select = (data) => {
                value.value= data.projectName
                focus.value= false
                page.projects=  list_copy.filter(item  => item.projectName === value.value)
            }


            /*  搜索 */
            const search= () => {
                if(!value.value){
                    list.value =  []
                    page.projects = list_copy
                }else{
                    list.value= list_copy.filter(item =>  item.projectName.includes(value.value))
                    page.projects = list.value
                }  
            }

            /* 跳到图片集合页面 */
            const toPics = (pro) => {
                console.log(userId,'userId')
                const {userId} = router.currentRoute.value.query
                router.push({
                    path: "/pics",
                    query: {proId: pro.id, userId}
                });
            }


            const showDeleteConfirm = (data) => {
                Modal.confirm({
                    title: () => '确定要删除这个项目吗?',
                    icon: () => createVNode(ExclamationCircleFilled),
                    content: () => '项目资源删除本质为失效，数据库留有记录',
                    okText: () => '确认',
                    okType: 'danger',
                    cancelText: () => '取消',

                    onOk() {
                       delPro(data)
                    },

                    onCancel() {
                    },
                });
            };
           
                

            return {
                page,
                 wh:[],  //左右及上下块边距
                 spanValue: 6, //设置这里展示每行展示 24 / column的向上取值的列
                 value,
                 list,
                 curIndex,
                 focus,
                 showBox,
                 curValue,
                 did,
                 select,
                 search,
                 formRef,
                 labelCol: {
                    span: 4,
                 },
                 wrapperCol: {
                    span: 14,
                 },
                 formState,
                 rules,
                 onSubmit,
                 toPics,
                 delPro,
                 showDeleteConfirm 
                 

            }
        }
    }
</script>

